.topic {
    grid-area: tp;
    background-color: grey;
    display: grid;
    grid-template-areas:
    "tr f";
    grid-template-columns: 3fr 0.7fr;
    grid-template-rows: 1fr;
    padding: 5px;

}

.topic img {
    width: 100%;
    height: 250px;
    border-radius: 20px;
}

.topicPicture {
    grid-area: tr;
    font-size: 20px;
    border: 6px solid #26a69a;
    border-radius: 20px;
}

