.friends {
    grid-area: f;
    display: grid;
    grid-template-areas:
    "a b c"
    "d e f"
    "g h i";
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    background-color: grey;
    font-size: 20px;
    border: 6px solid #26a69a;
    border-radius: 20px;
    margin-left: 5px;
}