.AvatarInfo {
    grid-area: ai;
    display: grid;
    grid-template-areas:
    "av inf";
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 5fr;
    background-color: grey;
    padding-right: 5px;
}

.avatar_status{
    grid-area: av;
    padding: 5px;
}
.avatar img {
    height: 250px;
    padding: 10px;
    border-radius: 10px;
}
.upload_img{
    padding: 5px;
}
.info {
    display: grid;
    grid-template-areas:
    "il am";
    grid-template-rows: 1fr;
    grid-template-columns: 2fr 2fr;
    grid-area: inf;
    background-color: grey;
    padding: 20px;
    font-size: 20px;
    border: 6px solid #26a69a;
    border-radius: 20px;
}

.info_left{
    grid-area: il;
}

.about_me{
    grid-area: am;
}

.contact {
    padding-left: 20px;
}

.status{
    background-color: grey;
    padding: 5px;
    font-size: 20px;
    border: 6px solid #26a69a;
    border-radius: 20px;
}